___CSS_LOADER_EXPORT___.push([module.id, `* {
  box-sizing: border-box;
  font-family: system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,
    Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  margin: 0;
  color: var(--text-color);
  transition: color var(--theme-transition), background-color var(--theme-transition);
}

:root {
  /* Light mode colors */
  --bg-color: #f9faf8;
  --card-bg: #fff;
  --text-color: #333;
  --text-secondary: #666;
  --input-bg: #dadada;
  --shadow: rgba(0, 0, 0, 0.1);

  /* Transition timing */
  --theme-transition: 0.3s ease;

  /* Shades of blue */
  --federal-blue: #03045eff;
  --marian-blue: #023e8aff;
  --honolulu-blue: #0077b6ff;
  --blue-green: #0096c7ff;
  --pacific-cyan: #00b4d8ff;
  --vivid-sky-blue: #48cae4ff;
  --non-photo-blue: #90e0efff;
  --non-photo-blue-2: #ade8f4ff;
  --light-cyan: #caf0f8ff;
}

/* Dark mode colors */
[data-theme="dark"] {
  --bg-color: #1a1a1a;
  --card-bg: #2d2d2d;
  --text-color: #f0f0f0;
  --text-secondary: #b0b0b0;
  --input-bg: #404040;
  --shadow: rgba(0, 0, 0, 0.3);
}

/* Dark mode SVG filters */
[data-theme="dark"] .info-group img {
  filter: invert(1) brightness(0.9);
}

/* Optional: Different filter for weather icons */
[data-theme="dark"] .weather-icon {
  filter: brightness(0.9) contrast(1.1);
}

/* Add transition for all SVGs */
.info-group img,
.weather-icon {
  transition: filter var(--theme-transition);
}

body,
html {
  height: 100%;
  background-color: var(--bg-color);
  margin: 0;
  transition: background-color var(--theme-transition);
}

main {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 2rem;
  height: max-content;
  min-height: 100%;
}

#app {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  width: 75vw;
  background-color: var(--card-bg);
  border-radius: 0.5rem;
  box-shadow: 0 2px 8px var(--shadow);
  padding: 3rem;
  text-align: center;
  transition: background-color var(--theme-transition), box-shadow var(--theme-transition);
}

.title {
  font-size: 2.5rem;
  line-height: 1;
  min-width: max-content;
}

form {
  display: flex;
  gap: 1rem;
  width: 100%;
  justify-content: center;
}
@media (max-width: 640px) {
  form {
    flex-direction: column;
  }

  form input {
    min-height: 3rem;
  }

  main {
    padding: 1rem;
  }

  #app {
    padding: 2rem;
    width: 100%;
  }

  .secondary-info {
    display: grid;
    grid-template-columns: 1fr 1fr;
  }
}

form > * {
  width: 100%;
}

form input {
  flex: 1 1 75%;
}

form button {
  flex: 1 1 25%;
}

#search {
  border-color: transparent;
  height: 3rem;
  padding: 0 1rem;
  border-radius: 0.5rem;
  background-color: var(--input-bg);
  color: var(--text-color);
  font-size: 1.125rem;
  transition: background-color var(--theme-transition), color var(--theme-transition);
}

.submitBtn {
  all: unset;
  padding: 0.5rem 2rem;
  border-radius: 0.5rem;
  background-color: var(--marian-blue);
  color: #fff;
  font-size: 1rem;
  cursor: pointer;
  transition: background-color 0.3s;
}

.submitBtn:hover {
  background-color: var(--honolulu-blue);
}

.weather-section {
  margin-top: 1.5rem;
  display: flex;
  flex-direction: column;
  gap: 2rem;
}

.city-name {
  font-size: 2rem;
  font-weight: 400;
}

.main-info {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0;
}
.weather-icon {
  height: 4rem;
  margin: 1rem;
}

.current-temp {
  font-size: 2rem;
  font-weight: 400;
}
.conditions {
  font-weight: 300;
}

.secondary-info {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(120px, 1fr));
  gap: 1rem;
  align-items: center;
}

.info-group {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
  min-width: max-content;
}

.info-group img {
  height: 3rem;
}

h3 {
  width: max-content;
}
